import { Component, OnInit, Input, Output, EventEmitter, Inject, AfterViewInit, ViewChild, ChangeDetectorRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators, FormArray } from '@angular/forms';
import { Observable } from 'rxjs';

import { ProductSubcategoryMasterService, CurrentUserService,ErrorHandleService } from '../../../../shared';
import { jqxComboBoxComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxcombobox';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { jqxValidatorComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxvalidator';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';

@Component({
  selector: 'app-product-subcategory-add',
  templateUrl: './product-subcategory-add.component.html',
  styleUrls: ['./product-subcategory-add.component.scss']
})
export class ProductSubcategoryAddComponent implements OnInit {
  @Output() productSubcategoryAdded = new EventEmitter();
  @Output() openCategoryAdd = new EventEmitter();
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;
  @ViewChild('categoryCombo') categoryCombo: jqxComboBoxComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;

  @Input('parentCategoryAdded')  set parentCategoryAdded(name: boolean) {
    this.catLoader = true;
    this.categoryAdapter.dataBind();
  }
  
    psCForm: FormGroup;
    post:any;
    categorysource: any;
    categoryAdapter: any;
    apiUrl: string;
    catLoader: boolean = false;

    userData: any = {};

    constructor(
      private fb: FormBuilder,
      private pscm: ProductSubcategoryMasterService,
      private ehs: ErrorHandleService,
      @Inject('API_URL') apiUrl: string,
      private cdr: ChangeDetectorRef,
      private cus: CurrentUserService
    ) { 
      this.apiUrl = apiUrl;
      this.userData = this.cus.getTokenData();
      this.psCForm = fb.group({
        'code': [null, Validators.required],
        'name': [null, Validators.required],
        'category_id': [null, Validators.required]
      });
    }
  
    ngOnInit() {
      this.catLoader = true;
      this.setCategoryCombo();
    }

    ngAfterViewInit(){
      // this.jqxLoader.open();
      this.cdr.detectChanges();
    }

    setCategoryCombo() {
        this.categorysource =
        {
            datatype: "json",
            contentType: "application/json; charset=utf-8",
            datafields: [
                { name: 'id'},
                { name: 'code'},
              ],
            id: 'id',
            data: this.userData,
            // async: false,
            url: this.apiUrl+'product_category?limit=all'
        };
    
        this.categoryAdapter = new jqx.dataAdapter(this.categorysource, {
          autoBind: true,
          downloadComplete: (data, status, xhr) => {
              let tData = data.data;
              this.catLoader = false;
              // this.jqxLoader.close();
              // for (var _i = 0; _i < tData.length; _i++) {
              //   tData[_i]['label'] = tData[_i]['code'];
              //   tData[_i]['category_id'] = tData[_i]['id'];
              // }
              // console.log(tData);
              return tData;
          },
          bindingComplete: () => {
          }
        });

    }
  
    addProductCategory(value: any){
      this.jqxLoader.open();
      value['category_id'] = this.categoryCombo.getSelectedItem().value;
      console.log(value);
      this.pscm.store(value)
          .subscribe(
            result => {
              let data = this.ehs.formatError(result);
              if (data['result'] == true) {
                this.productSubcategoryAdded.emit(data);
              } else {
                let messageDiv: any = document.getElementById('windowMessage');
                messageDiv.innerHTML = '';
                for (let i = 0; i < data['message'].length; i++) {
                  let errorDiv = document.createElement('li');
                  errorDiv.innerText = data['message'][i];
                  messageDiv.appendChild(errorDiv);
                }
                this.winNotification.open();
              }
              this.jqxLoader.close();
            },
            error => {
              let data = this.ehs.formatError(error);
              let messageDiv: any = document.getElementById('windowMessage');
              messageDiv.innerText = data['message'];
              this.winNotification.open();
              this.jqxLoader.close();
            }
          )
    }
    
  
    clearForm(): void {
      this.psCForm.reset();
    }

    addCategory(){
      this.openCategoryAdd.emit('open');
    }

    rules =
    [
        { input: '.input_code', message: 'Code is required!', action: 'keydown, blur', rule: 'required' },
        { input: '.input_name', message: 'Name is required!', action: 'keydown, blur', rule: 'required' },
        
    ];


}



