import { Component, OnInit, Inject, ViewChild, AfterViewInit, ViewContainerRef, ComponentFactoryResolver, TemplateRef } from '@angular/core';
import { Observable } from 'rxjs';
import { ProductSubcategoryMasterService, QuickaccessComponent, FileExportService, CurrentUserService } from '../../../shared';
import { jqxNotificationComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxnotification';
import { jqxLoaderComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxloader';
import { ProductSubcategoryAddComponent } from './product-subcategory-add/product-subcategory-add.component';
import { ProductSubcategoryEditComponent } from './product-subcategory-edit/product-subcategory-edit.component';
import { ProductCategoryAddComponent } from '../product-category-master/product-category-add/product-category-add.component';
import { jqxGridComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxgrid';
import { jqxWindowComponent } from 'jqwidgets-framework/jqwidgets-ts/angular_jqxwindow';
import * as pdfMake from 'pdfmake/build/pdfmake';
import * as pdfFonts from 'pdfmake/build/vfs_fonts';

@Component({
  selector: 'app-product-subcategory-master',
  templateUrl: './product-subcategory-master.component.html',
  styleUrls: ['./product-subcategory-master.component.scss']
})
export class ProductSubcategoryMasterComponent implements OnInit {
  @ViewChild('msgNotification') msgNotification: jqxNotificationComponent;
  @ViewChild('jqxLoader') jqxLoader: jqxLoaderComponent;
  @ViewChild('dynamicInsert', { read: ViewContainerRef }) dynamicInsert: ViewContainerRef;
  @ViewChild('dynamicInsertCategory', { read: ViewContainerRef }) dynamicInsertCategory: ViewContainerRef;
  @ViewChild('myGrid') myGrid: jqxGridComponent;
  @ViewChild('windowReference') window: jqxWindowComponent;
  @ViewChild('addCategory') addCategoryWindow: jqxWindowComponent;
  @ViewChild('quickAccess', { read: ViewContainerRef }) quickAccess: ViewContainerRef;
  @ViewChild('winNotification') winNotification: jqxNotificationComponent;

  apiUrl: string;
  source: any = {};
  dataAdapter: any;
  columns: any[];
  editrow: number = -1;
  dyynamicComponent: any;
  dynamicCategoryComponent: any;

  userData: any = {};

  constructor(
    @Inject('API_URL') apiUrl: string,
    private pscm: ProductSubcategoryMasterService,
    private componentFactoryResolver: ComponentFactoryResolver,
    private fileExport: FileExportService,
    private cus: CurrentUserService,
  ) {
    this.apiUrl = apiUrl;
    this.userData = this.cus.getTokenData();
    this.getTable();
  }

  ngOnInit() {
    this.createQuickAccess();
  }

  ngAfterViewInit(): void {

  }

  createQuickAccess() {
    const componentFactory = this.componentFactoryResolver.resolveComponentFactory(QuickaccessComponent);
    this.quickAccess.clear();
    const dyynamicComponent = this.quickAccess.createComponent(componentFactory);
  }

  /**
   * initialize grid variable
   */
  getTable() {
    let myUrl = this.apiUrl + 'product_sub_category';
    let totalData: number;
    this.source =
      {
        datatype: 'json',
        //   localData: {},
        // datafields: [
        //   { name: 'id', type: 'string' },
        //   { name: 'code', type: 'string' },
        //   { name: 'name', type: 'int' },
        //   { name: 'category_id', type: 'int'},
        // ],
        id: 'id',
        root: 'data',
        pagesize: 20,
        data: this.userData,
        url: myUrl,
        totalrecords: 100,
        beforeprocessing: (data) => {

        }
      }

    /**
     * Define Data Adapter for grid source
     * Help in Formating data before making server request, 
     * As well as change the response data format
     */
    this.dataAdapter = new jqx.dataAdapter(this.source, {
      //before sending request to server format params in laravel pagination params type
      formatData: (data) => {
        console.log(data);
        data['page'] = data['pagenum'] + 1;
        data['limit'] = data['pagesize'];
        return data;
      },
      // after succesful response from server change data format to suitable form before rendering to grid.
      downloadComplete: (data, status, xhr) => {
        // if (this.source && this.source.totalrecords) {
        this.source.totalrecords = data.data.total;
        // }
        let tData = data.data.data;
        return tData;
      }, loadError: (jqXHR, status, error) => {
        let messageDiv: any = document.getElementById('listingMessage');
        messageDiv.innerText = error;
        this.winNotification.open();
        this.jqxLoader.close();
      },
      loadComplete: (data) => {
      }
    });

    /**
     * Define column name, value & event handler to grid columns
     */
    this.columns =
      [
        { text: 'Id', datafield: 'id' },
        { text: 'Code', datafield: 'code' },
        { text: 'Name', datafield: 'name' },
        {
          text: 'Edit', datafield: 'Edit', columntype: 'button', width: 70,
          cellsrenderer: (): string => {
            return 'Edit';
          },
          buttonclick: (row: number): void => {
            this.window.setTitle("Edit Product SubCategory");
            //get the data and append in to the inputs
            this.editrow = row;
            let dataRecord = this.myGrid.getrowdata(this.editrow);
            console.log(dataRecord);
            const componentFactory = this.componentFactoryResolver.resolveComponentFactory(ProductSubcategoryEditComponent);
            this.dynamicInsert.clear();
            this.dyynamicComponent = this.dynamicInsert.createComponent(componentFactory);
            (<ProductSubcategoryEditComponent>this.dyynamicComponent.instance).productCategoryData = dataRecord;
            (<ProductSubcategoryEditComponent>this.dyynamicComponent.instance).productSubcategoryUpdated.subscribe(
              result => {
                if (result == true) {
                  this.window.close();
                  this.myGrid.updatebounddata('cells');
                }
              }
            );

            (<ProductSubcategoryEditComponent>this.dyynamicComponent.instance).openCategoryAdd.subscribe(
              result => {
                if (result === 'open') {
                  console.log(result);
                  this.createCategoryAddComponent('edit');
                  this.addCategoryWindow.open();
                  this.addCategoryWindow.focus();
                  // this.myGrid.updatebounddata('cells');
                }
              }
            );

            this.window.open();
          }
        }
      ];


  }


  /**
   * For Server Side Pagination
   * Return array of data in required format to be rendered in grid.
   */
  rendergridrows = (params: any): any[] => {
    console.log(params);
    return params.data;

  }

  /**
   * Render Toolbar with Add & Delete Button &
   * attach event handler to the buttons.
   */
  rendertoolbar = (toolbar: any): void => {
    let container = document.createElement('div');
    container.style.margin = '5px';

    let buttonContainer1 = document.createElement('div');
    let buttonContainer2 = document.createElement('div');
    let buttonContainer3 = document.createElement('div');
    let buttonContainer4 = document.createElement('div');
    let buttonContainer5 = document.createElement('div');
    let buttonContainer6 = document.createElement('div');

    buttonContainer1.id = 'buttonContainer1';
    buttonContainer2.id = 'buttonContainer2';
    buttonContainer3.id = 'buttonContainer3';
    buttonContainer4.id = 'buttonContainer4';
    buttonContainer5.id = 'buttonContainer5';
    buttonContainer6.id = 'buttonContainer6';

    buttonContainer1.style.cssText = 'float: left';
    buttonContainer2.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer3.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer4.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer5.style.cssText = 'float: left; margin-left: 5px';
    buttonContainer6.style.cssText = 'float: left; margin-left: 5px';

    container.appendChild(buttonContainer1);
    container.appendChild(buttonContainer3);
    container.appendChild(buttonContainer2);
    container.appendChild(buttonContainer4);
    container.appendChild(buttonContainer5);
    container.appendChild(buttonContainer6);
    toolbar[0].appendChild(container);

    let addRowButton = jqwidgets.createInstance('#buttonContainer1', 'jqxButton', { width: 120, value: '<i class="fa fa-plus fa-fw"></i> Add New Row', theme: 'energyblue' });
    let deleteRowButton = jqwidgets.createInstance('#buttonContainer3', 'jqxButton', { width: 150, value: 'Delete Selected Row', theme: 'energyblue' });
    let reloadGridButton = jqwidgets.createInstance('#buttonContainer2', 'jqxButton', { width: 80, value: '<i class="fa fa-refresh fa-fw"></i> Reload', theme: 'energyblue' });
    let exportExcelButton = jqwidgets.createInstance('#buttonContainer4', 'jqxButton', { width: 120, value: '<i class="fa fa-file-excel-o fa-fw"></i> Export Excel', theme: 'energyblue' });
    let exportPdfButton = jqwidgets.createInstance('#buttonContainer5', 'jqxButton', { width: 110, value: '<i class="fa fa-file-pdf-o fa-fw"></i> Export PDF', theme: 'energyblue' });
    let printButton = jqwidgets.createInstance('#buttonContainer6', 'jqxButton', { width: 110, value: '<i class="fa fa-print fa-fw"></i> Print', theme: 'energyblue' })
    /**
     * Add a button Event Handler in grid toolbar to open window with initialized Add Subcategory component inside window
     */
    addRowButton.addEventHandler('click', () => {
      this.window.setTitle("Add Product SubCategory");
      const componentFactory = this.componentFactoryResolver.resolveComponentFactory(ProductSubcategoryAddComponent);
      this.dynamicInsert.clear();
      this.dyynamicComponent = this.dynamicInsert.createComponent(componentFactory);

      /**
       * Subscribe to the Event emmited from SubCategory Add Component,
       *  when Sub Category has been added.
       */
      (<ProductSubcategoryAddComponent>this.dyynamicComponent.instance).productSubcategoryAdded.subscribe(
        result => {
          if (result == true) {
            this.window.close();
            this.myGrid.updatebounddata('cells');
          }
        }
      );

      /**
       * Subscribe to the Event emitted from SubCategory Add Component,
       * To open Category Add Component.
       */
      (<ProductSubcategoryAddComponent>this.dyynamicComponent.instance).openCategoryAdd.subscribe(
        result => {
          if (result === 'open') {
            console.log(result);
            this.createCategoryAddComponent('add');
            this.addCategoryWindow.open();
            this.addCategoryWindow.focus();
            // this.myGrid.updatebounddata('cells');
          }
        }
      );
      this.window.open();
    });

    /**
     * Add a button Event handler in grid toolbar to delete Selected row
     * remove row from grid row as well as send delete request to server
     */
    deleteRowButton.addEventHandler('click', () => {
      let id = this.myGrid.getselectedrowindexes();
      let ids = [];
      let rowscount = this.myGrid.getdatainformation().rowscount;
      for (let i = 0; i < id.length; i++) {
        let dataRecord = this.myGrid.getrowdata(Number(id[i]));
        ids.push(dataRecord.id);
      }
      if (ids.length > 0 && ids.length <= parseFloat(rowscount)) {
        if (confirm("Are you sure? You Want to delete")) {
          // let id = this.myGrid.getrowid(selectedrowindex);
          this.jqxLoader.open();
          this.pscm.destroy('[' + ids + ']').subscribe(data => {
            let messageDiv = document.getElementById('message');
            messageDiv.innerText = data['message'];
            this.msgNotification.open();
            this.jqxLoader.close();
            this.myGrid.updatebounddata('cells');
          })
        }
      } else {
        let messageDiv = document.getElementById('message');
        messageDiv.innerText = 'Please select some item to delete';
        this.msgNotification.open();
      }
    })

    reloadGridButton.addEventHandler('click', () => {
      this.myGrid.updatebounddata('cells');
    });

    exportExcelButton.addEventHandler('click', () => {
      // let currentDate = new Date().toLocaleDateString();
      // let fileName = 'Product-Category-'+ currentDate;
      // this.myGrid.exportdata('xls', 'Product-Category-Master');
      let post: any = {};
      this.pscm.index(post)
        .subscribe(
        data => {
          // set items to json response
          console.log(data);
          let exportData = data;
          let fileName = this.fileExport.getFileName('product-sub-category');
          this.fileExport.downloadFile(exportData, fileName);
        },
        error => {
          console.log(error);

        });
    });

    exportPdfButton.addEventHandler('click', () => {
      let type: string = 'download';
      this.createPdfFormat(type);
      // this.myGrid.exportdata('pdf', 'Product-Category-Master');
    });

    printButton.addEventHandler('click', () => {
      let type: string = 'print';
      this.createPdfFormat(type);
    });


  };

  // addedNewEntry(event){
  //   if(event == true){
  //     (<ProductSubcategoryAddComponent>this.dyynamicComponent.instance).parentCategoryAdded = true;
  //   }
  //   this.addCategoryWindow.close();
  // }


  fromComponent: string;
  /**
   * Initialize Category component inside view container &
   * subscribe to changes inside of category add component 
   */
  createCategoryAddComponent(fromComponent) {

    this.fromComponent = fromComponent;
    const subComponentFactory = this.componentFactoryResolver.resolveComponentFactory(ProductCategoryAddComponent);
    this.dynamicInsertCategory.clear();
    this.dynamicCategoryComponent = this.dynamicInsertCategory.createComponent(subComponentFactory);
    (<ProductCategoryAddComponent>this.dynamicCategoryComponent.instance).productCategoryAdded.subscribe(
      result => {
        console.log(result);
        if (result == true && this.fromComponent == 'add') {
          (<ProductSubcategoryAddComponent>this.dyynamicComponent.instance).parentCategoryAdded = true;
        }
        if (result == true && this.fromComponent == 'edit') {
          (<ProductSubcategoryEditComponent>this.dyynamicComponent.instance).parentCategoryAdded = true;
        }

        this.addCategoryWindow.close();
        this.dynamicCategoryComponent.destroy();
      }
    );
  }

  /**
   * Called when Category Add window is closed &
   * Category add component is Destroyed
   * @param event 
   */
  closeCategory(event) {
    this.dynamicCategoryComponent.destroy();
  }

  /**
   * 
   * @param type 
   */
  createPdfFormat(type: string) {
    let rows = [];
    let post: any = {};
    pdfMake.vfs = pdfFonts.pdfMake.vfs;

    this.pscm.index(post)
      .subscribe(
      data => {
        // set items to json response
        let exportData = data;
        // Define rows as array to push table rows data.
        let rows = [];
        // Define first row for table to be displayed as header.
        let header = ['Id', 'Code', 'Name'];
        // Push Firt row into rows array.
        rows.push(header);
        // Format required data & field to be displayed in pdf.
        for (let i = 0; i < exportData.length; i++) {
          // Define a column array for the i(th) row.
          let cols = [];
          cols.push(exportData[i].id);
          cols.push(exportData[i].code);
          cols.push(exportData[i].name);
          // Push the columns array ie a single row into rows array.
          rows.push(cols);
        }

        // Define PDF Doc
        let dd = {
          pageSize: 'A4',
          content: [
            {
              table: {
                // headers are automatically repeated if the table spans over multiple pages
                // you can declare how many rows should be treated as headers
                headerRows: 1,
                widths: ['*', '*', '*', '*'],
                // Set the rows array into body of the table.
                body: rows
              }
            }
          ],
        };

        // createPdf with the provided file name & Download it
        // pdfMake.createPdf(dd).getDataUrl(function(outDoc) {
        //   console.log(outDoc);
        //   document.getElementById('pdfPlugin')['src'] = outDoc;
        // });
        if (type == 'download') {
          let fileName = this.fileExport.getFileName('product-sub-category');
          pdfMake.createPdf(dd).download(fileName);
        } else {
          pdfMake.createPdf(dd).print();
        }


      },
      error => {
        console.log(error);
      });
  }


  /**
   * Called when Navigation away from this component
   */
  canDeactivate() {
    // console.log('i am navigating away');
    // alert('Navigating Away');
    // if the editName !== this.user.name
    // if (this.user.name !== this.editName) {
    // return window.confirm('Are you sure you want to leave?');
    // }
    return true;
  }

}



