import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';

import { SharedModule } from '../../../shared/modules/shared.module';
// import { ProductCategorySharedModule } from '../../../shared/modules/product_category_shared.module';

import { ProductSubcategoryMasterRoutingModule } from './product-subcategory-master-routing.module';
import { ProductSubcategoryMasterComponent } from './product-subcategory-master.component';
import { ProductSubcategoryAddComponent } from './product-subcategory-add/product-subcategory-add.component';
import { ProductSubcategoryEditComponent } from './product-subcategory-edit/product-subcategory-edit.component';
// import { ProductCategoryAddComponent } from '../product-category-master/product-category-add/product-category-add.component';

@NgModule({
  imports: [
    CommonModule,
    SharedModule,
    ProductSubcategoryMasterRoutingModule,
    // ProductCategorySharedModule,
  ],
  declarations: [
    ProductSubcategoryMasterComponent,
    ProductSubcategoryAddComponent,
    ProductSubcategoryEditComponent,
  ],
  entryComponents:[
    ProductSubcategoryAddComponent,
    ProductSubcategoryEditComponent,
    // ProductCategoryAddComponent,
  ],
  schemas: [
    CUSTOM_ELEMENTS_SCHEMA,
  ]
})
export class ProductSubcategoryMasterModule { }



